<template>
  <div class="logo-generator">
    <!-- 입력 폼 -->
    <textarea v-model="inputText" placeholder="텍스트를 입력하세요"></textarea>

    <!-- 로고 미리보기 -->
    <div v-if="inputText" class="logo-previews">
      <div v-for="(style, index) in logoStyles" :key="index" class="logo-preview" :style="style.style">
        {{ inputText }}
      </div>
    </div>

    <!-- 이미지 다운로드 버튼 -->
    <button v-if="inputText" @click="downloadLogos">로고 다운로드</button>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'

export default {
  name: 'LogoGenerator',
  data () {
    return {
      inputText: 'QR FONT',
      logoStyles: [
        { name: 'Style 1', style: { fontSize: '48px', fontFamily: 'Arial, sans-serif', fontWeight: 'bold', color: '#FF5733', backgroundColor: '#F0F0F0', padding: '20px', border: '2px solid #CCCCCC' } },
        { name: 'Style 2', style: { fontSize: '40px', fontFamily: 'Impact, sans-serif', fontWeight: 'normal', color: '#4CAF50', backgroundColor: '#FFFF00', padding: '15px', border: '3px dashed #FF5722' } },
        { name: 'Style 3', style: { fontSize: '36px', fontFamily: 'Tahoma, sans-serif', fontWeight: 'bold', color: '#2196F3', backgroundColor: '#E0E0E0', padding: '10px', border: '1px dotted #9C27B0' } },
        { name: 'Style 4', style: { fontSize: '42px', fontFamily: 'Georgia, serif', fontWeight: 'bold', color: '#FFC107', backgroundColor: '#FFFFFF', padding: '25px', border: '2px solid #607D8B' } },
        { name: 'Style 5', style: { fontSize: '44px', fontFamily: 'Courier New, monospace', fontWeight: 'normal', color: '#9C27B0', backgroundColor: '#F44336', padding: '18px', border: '3px double #03A9F4' } },
        { name: 'Style 6', style: { fontSize: '38px', fontFamily: 'Palatino Linotype, serif', fontWeight: 'bold', color: '#E91E63', backgroundColor: '#FFEB3B', padding: '22px', border: '2px solid #795548' } },
        { name: 'Style 7', style: { fontSize: '46px', fontFamily: 'Comic Sans MS, cursive', fontWeight: 'normal', color: '#795548', backgroundColor: '#4CAF50', padding: '20px', border: '3px dashed #FF5722' } },
        { name: 'Style 8', style: { fontSize: '32px', fontFamily: 'Verdana, sans-serif', fontWeight: 'bold', color: '#607D8B', backgroundColor: '#03A9F4', padding: '12px', border: '2px solid #FFC107' } },
        { name: 'Style 9', style: { fontSize: '50px', fontFamily: 'Times New Roman, serif', fontWeight: 'normal', color: '#FF5722', backgroundColor: '#F0F0F0', padding: '30px', border: '1px dotted #9C27B0' } },
        {
          name: 'Style 10',
          style: {
            textTransform: 'Impact',
            background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
            webkitBackgroundClip: 'text',
            webkitTextFillColor: 'transparent',
            fontSize: '50px',
            fontFamily: 'Arial, sans-serif'
          }
        }
      ]
    }
  },
  methods: {
    downloadLogos () {
      const logoElements = document.querySelectorAll('.logo-preview')

      logoElements.forEach((logoElement, index) => {
        html2canvas(logoElement)
          .then(canvas => {
            const dataUrl = canvas.toDataURL('image/png')
            const link = document.createElement('a')
            link.href = dataUrl
            link.download = `logo_${index + 1}.png`
            document.body.appendChild(link)
            link.click()
            document.body.removeChild(link)
          })
          .catch(error => {
            console.error(`로고 ${index + 1} 다운로드 오류:`, error)
          })
      })
    }
  }
}
</script>

<style scoped>
.logo-generator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: 'Arial', sans-serif;
}

textarea {
  width: 300px;
  height: 100px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

.logo-previews {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.logo-preview {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  width: 300px;
  margin-top: 20px;
  overflow: hidden;
  white-space: nowrap;
}

button {
  padding: 10px 20px;
  margin-top: 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}
</style>
